body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: sans-serif;
}
.header{
  height: 90px;
  background-color: #fff;
  width: 100%;
  padding: 3%;
  border-top: solid 2px purple;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.logo{
  width: 160px;
}

.menu{
  font-weight: 600;
}
.navbar-nav a{
  color: purple;
}

.btn-C{
  color:purple;
  border: solid 1px purple;
  border-radius: 8px;
  padding: 0.5% 1%;
  text-decoration: none;

}

.btn-C:hover{
  background-color: purple;
  color: #fff;
}

.home-1st{
  background: url(/static/media/2.3b928cef.jpg), purple;
  background-size: cover;
  margin-top: 90px;
  padding: 5% 3%;
}

.home-1st-text{
  color: #fff;
  text-shadow: 1px 1px 2px #000;
  align-self: center !important;
}

.home-1st-text h1{
  font-size: 3em;
  font-weight: bolder;
}

.home-1st-text p{
  font-size: 14pt;
}

.home-1st-booking{
  /*background: rgb(33,0,51);
  background: linear-gradient(90deg, rgba(33,0,51,0.49625787815126055) 0%, rgba(255,255,255,0.10970325630252098) 50%, rgba(4,49,17,0.5046612394957983) 100%);
  */
  background-color: rgba(0,0,0,0.5);
  padding: 2%;
  color: #fff;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  border-radius: 20px;
 
}

.booking-page{
  background-color: #fff;
  color: purple;
  padding: 2%;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  border-radius: 20px;
}

.booking-page #departureDate, .booking-page select{
  border: solid 1px purple !important;
  color: purple !important;
}

.middle-content{
  background-color: #fff;
  box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.2);
  padding: 2% 3% !important;
}

.nav-item button{
  background-color: transparent;
  border: none;
  margin-right: 10px;
  color: rgba(255,255,255,0.7);
  font-weight: bold;
}

.nav-item .active{
  background-color:rgba(0, 0, 0, 0.5);
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  border-radius: 8px;
  color: #fff !important;
}

.nav-item button:hover, .nav-item .active:active, .nav-item .active:link{
  color: #fff !important;
}

.nav-pill{
  border-bottom: solid 1px #fff;
  padding-bottom: 2%;
}

.bookingForm select{
  background-color: transparent;
  color: #fff;
  border-color: #fff
}

.form-item{
  margin-bottom: 4%;
}

#departureDate{
  background-color: transparent;
  color: #fff;
  border-color: #fff;
}

#btn-book{
  background-color: rgb(90, 2, 90);
  color: #fff;
  padding: 2%;
  text-align: center;
  border:none;
  box-shadow: 0px 8px 16px 0px rgba(255,255,255,0.5);
  font-weight: bold;
  border-radius: 10px;
}

#code-input{
  background-color: transparent;
  color: #fff;
  border-color: #fff;
}

#code-input::-webkit-input-placeholder{
  color: #fff;
  font-size: small;
  font-style: oblique;
}

#code-input::placeholder{
  color: #fff;
  font-size: small;
  font-style: oblique;
}

.travel{
  width:100%;
  padding:7% 3%;
  color: purple;
  background: rgba(90, 2, 90, 0.1);
}



.travel-text h1{
  font:  bolder 3.5em sans-serif;

}

.travel-text p{
  font-weight: bold;
  color: #000;

}

.travel-photo{
  position: relative;
  height: 500px;
}

.travel-photo div{
  width: 280px;
  height: 280px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.5);
  position: relative;
  border-radius: 50%;
  padding: 0px;
}

.travel-photo .p1{
  position: absolute;
  top:-2%;
  left: 25%;
  background-image: url(/static/media/p2.2b85cd53.jpg);
  background-size: cover;
}

.travel-photo .p2{
  position: absolute;
  top:60%;
  left: 0%;
  background-image: url(/static/media/p3.ace80bc9.jpg);
  background-size: cover;
}

.travel-photo .p3{
  position: absolute;
  top:60%;
  left: 50%;
  background-image: url(/static/media/p1.825f84da.jpg);
  background-size: cover;
}


@media only screen and (max-width: 600px) {
  .travel-photo .p1, .travel-photo .p2, .travel-photo .p3{
    width: 180px;
    height: 180px;
  }
  .travel-photo .p2, .travel-photo .p3{
    top:35%;
  }
}

.content{
  padding:7% 3%;
  margin-top:90px ;
  background: rgba(90, 2, 90, 0.1);
}

.four-0-four img{
  width: 100%;
}

.pay-photo{
  position: relative;
}

.pay-photo .photo1{
  width:60%;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  z-index:1;
}

.pay-photo .photo2{
  width:60%;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  left:40%;
  top:10%;
  position:absolute;
  z-index:-1;
}

@media only screen and (max-width: 600px) {
  .pay-photo{
    margin-bottom: 10%;
  }
  
}

.home-cargo{
  padding: 5% 3%;
}

.cargo-photos img{
  width:100%;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.5);
}

.travel-text h3{
  color:purple;
  font-size: 2em;
  font-weight: bolder;
  line-height: 3em;
}

.btn-subscribe{
  background-color: purple !important;
  color: #fff !important;
  border: none !important;
  font-weight: bold !important;
  box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.5);
}

.btn-subscribe:hover{
  background-color: #fff !important;
  color: purple !important;
  border: solid 1px purple!important;
}

.subscribe{
  padding: 8% 3%;
  background-color: rgba(170, 224, 247, 0.496);
}

.subscribe-text{
  font-size: 14.5pt;
  font-weight: 600;
  color: purple;
}

.footer {
  padding: 5% 3%;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  background-color: #f5f5f5;
}

.footer h3{
  font-weight: 900;
  font-family: 'Trebuchet MS', sans-serif;
}

.footer .quick-links ul li{
  list-style-type:square;
}

.quick-links a{
  text-decoration: none;
  color: #000;
}
.author{
  border-top: solid 1px purple;
  font-size: 14px;
  font-weight: 500;
  padding:1%;
}
